@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";
.projects {
    overflow: hidden;
    position: sticky;
    top: 0;
    padding: 35px 0;
    text-align: left;
    color: #000;
    font-size: 23px;
    background-color: #E5E5E5;
    z-index: 3;
    min-height: 200vh;
    min-height: 100vh;
    height: auto;
    h1 {
        padding-left: 45px;
        margin-bottom: 15%;
    }
}

.projects__slider {
    margin-top: 50%;
    width: auto;
    margin: 0 auto;
    outline: none;
    border: none;
    a {
        cursor: pointer;
        outline: none;
        position: relative;
        img {
            /*//height: 32.5vh;
            //width: 24.5vw;*/
            height: 57.5vh;
            width: 23.75vw;
            //border-radius: 10px;
            margin: 0 auto;
            object-fit: cover;
            object-position: center;
            outline: none;
            border: none;
        }
        .project__info {
            position: absolute;
            bottom: 0;
            left: 2.5%;
            color: white;
            font-size: 2rem;
            background: rgb(0, 0, 0);
            background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgb(20, 33, 63) 2.5%, rgba(255, 255, 255, 0) 100%);
            left: 50%;
            transform: translate(-50%, 0%);
            width: 95%;
            padding: 17.5% 2.5% 10%;
            text-align: center;
            //border-radius: 10px;
            text-shadow: 1px 1px 1px #000;
        }
        &:active {
            cursor: grab;
        }
    }
}

@media screen and (max-width: 768px) {
    .projects {
        background-color: #fff;
        position: static; //!! TO FIX
        h1 {
            padding-left: 0;
        }
    }
    .projects__slider {
        a {
            img {
                width: 90vw;
            }
            .project__info {
                width: 90%;
            }
        }
    }
}


