@import "normalize";
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*::-webkit-scrollbar {
    width: 0px;
}

html {
    scroll-behavior: smooth;
    overflow-x: hidden;
}

body {
    font-family: 'Open Sans', sans-serif;
    background-color: #14213D;
}

a {
    text-decoration: none;
}

.profile__picture__test {
    background-image: url(img/Hubert.png);
}

.aboutMe__picture {
    background: url(img/Hubert.png);
}

.skill__example:nth-of-type(1) .skill {
    background-image: url(img/HTML.png);
}

.skill__example:nth-of-type(2) .skill {
    background-image: url(img/CSS.png);
}

.skill__example:nth-of-type(3) .skill {
    background-image: url(img/SASS.png);
    opacity: 0.425;
}

.skill__example:nth-of-type(4) .skill {
    background-image: url(img/JScript.png);
    opacity: 0.425;
}

.skill__example:nth-of-type(5) .skill {
    background-image: url(img/React.png);
}

.skill__example:nth-of-type(6) .skill {
    background-image: url(img/GIT.png);
    opacity: 0.45;
}

.skill__example:nth-of-type(7) .skill {
    background-image: url(img/RWD.png);
}

.skill__example:nth-of-type(8) .skill {
    background-image: url(img/ReactRouter.png);
    opacity: 0.25;
}

.skill__example:nth-of-type(9) .skill {
    background-image: url(img/Redux.png);
}

.skill__example:nth-of-type(10) .skill {
    background-image: url(https://upload.wikimedia.org/wikipedia/commons/thumb/4/4c/Typescript_logo_2020.svg/1200px-Typescript_logo_2020.svg.png);
}

.skill__example:nth-of-type(11) .skill {
    background-image: url(https://mui.com/static/logo.png);
}

.skill__example:nth-of-type(12) .skill {
    background-image: url(https://upload.wikimedia.org/wikipedia/commons/thumb/b/b2/Bootstrap_logo.svg/800px-Bootstrap_logo.svg.png);
}

.project {
    &:first-of-type {
        background-image: url(img/project1.jpg);
    }
    &:nth-of-type(2) {
        background-image: url(img/restaurant.png);
    }
    &:nth-of-type(3) {
        background-image: url(img/TRVLstadiums.png);
        background-position: top !important;
    }
    &:nth-of-type(4) {
        background-image: url(img/oneArmed.png);
        background-position: top !important;
    }
    &:nth-of-type(5) {
        background-image: url(img/toDo.png);
        background-position: top !important;
    }
    &:nth-of-type(6) {
        background-image: url(img/PRS.png);
        background-position: top !important;
    }
    &:nth-of-type(7) {
        background-image: url(img/marle.png);
    }
    &:nth-of-type(8) {
        background-image: url(img/temperature.png);
    }
    &:nth-of-type(9) {
        background-image: url(img/bmi.png);
        background-position: top !important;
    }
    &:nth-of-type(10) {
        background-image: url(img/restaurant.png);
    }
}