.section__map {
    padding: 0;
    //image in the background
    .map__background {
        filter: blur(2px) brightness(0.6);
        position: absolute;
        height: 100vh;
    }
}

//mapa
.map {
    filter: brightness(1.5);
    // border-radius: 50%;
    //margin: 10vh auto 0;
    width: 100%; //80
    height: 100%; //75
    display: flex;
    justify-content: center;
    flex-direction: row;
    button,
    .gmnoprint {
        display: none;
    }
    >div:nth-child(1) {
        z-index: 1 !important;
    }
    >div {
        z-index: -5 !important;
    }
    div>div>div>div>div>div>div>div>div {
        display: none;
    }
}

.section__map {
    background-color: #E5E5E5;
    z-index: 6;
}