//FORM - MAILER
.form {
    font-size: 19px;
    display: flex;
    flex-direction: column;
    width: 40%;
    margin-top: 3.5vh; // do ewentualnego usunięcią
    button {
        text-transform: uppercase;
        font-size: 20px;
        padding: 15px;
        cursor: pointer;
        background-color: #fff;
        border: 2px solid #fff;
        color: #000;
        width: 85%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 35px auto 0;
        letter-spacing: 1.5px;
        svg {
            font-size: 22px;
            margin-left: 10px;
        }
        &:hover {
            background-color: transparent;
            color: #fff;
            border: 2px solid #fff;
        }
    }
    input {
        margin-top: 10px;
        padding: 5px 0;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #fff;
        color: #fff;
        transition: .1s;
        &:focus {
            outline: none;
            border-bottom: 3px solid #fff;
            border-width: 50%;
        }
    }
    textarea {
        margin-top: 10px;
        resize: none;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #fff;
        color: #fff;
        transition: .1s;
        overflow: hidden;
        &:focus {
            outline: none;
            border-bottom: 3px solid #fff;
        }
    }
    label {
        margin-top: 10%;
        font-size: 25px;
        text-transform: uppercase;
    }
}