.aboutMe__picture {
    //background: url(/img/Hubert.png);
    background-color: #14213D;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 50%;
    width: 17.5vh;
    height: 17.5vh;
    float: left;
    shape-outside: circle(50%);
    margin-right: 15px;
}

.aboutMe__paragraphs {
    text-align: justify;
}

.aboutMe__paragraph {
    font-size: 26px;
    padding-top: 7vh;
    word-spacing: 1px;
    &::first-letter {
        font-weight: bold;
        font-size: 35px;
    }
    &:first-of-type {
        padding-top: 4vh;
    }
}

@media screen and (max-width: 1440px) {
    .about__me {
        h1 {
            margin: 0 0 5%;
            text-align: center;
        }
    }
}

@media (orientation: landscape) and (min-width: 768px) and (max-width: 1180px) {
    .about__me {
        padding: 5vh 45px;
        h1 {
            margin: 0;
            text-align: center;
        }
        .aboutMe__paragraph {
            font-size: 24px;
            padding-top: 5vh;
            &::first-letter {
                font-size: 32px;
            }
        }
    }
}

@media (orientation: portrait) and (min-width: 768px) {
    .about__me {
        padding: 5vh 45px;
        h1 {
            margin: 0;
            text-align: center;
            font-size: 50px;
        }
        .aboutMe__paragraph {
            font-size: 32px;
            padding-top: 6vh;
            &::first-letter {
                font-size: 36px;
            }
        }
    }
}

@media screen and (max-width: 1024px) {
    .about__me {
        padding: 5vh 7vw;
        h1 {
            font-size: 50px;
            margin-bottom: 5vh;
        }
        .aboutMe__paragraph {
            font-size: 26px;
            padding-top: 6vh;
            word-spacing: 4px;
            &::first-letter {
                font-size: 36px;
            }
        }
        .aboutMe__picture {
            height: 20vh;
            width: 20vh;
        }
    }
}

@media screen and (max-width: 768px) {
    .about__me {
        position: sticky;
        z-index: -1;
        padding: 5vh 0;
        h1 {
            font-size: 50px;
        }
        .aboutMe__paragraph {
            font-size: 28px;
            padding-top: 5vh;
            word-spacing: 2px;
            &::first-letter {
                font-size: 34px;
            }
        }
    }
    .aboutMe__paragraphs {
        padding: 0 35px;
    }
}

@media screen and (max-width: 426px) {
    .about__me {
        h1 {
            font-size: 38px;
            margin-bottom: 5vh;
        }
        .aboutMe__paragraphs {
            padding: 0 5vw;
            .aboutMe__paragraph {
                font-size: 19px;
                padding-top: 4vh;
                word-spacing: 2px;
                &:first-of-type {
                    padding-top: 1vh;
                }
                &::first-letter {
                    font-size: 26px;
                }
            }
        }
    }
    .aboutMe__picture {
        margin: 0 15px;
        width: 14vh;
        height: 14vh;
    }
}