.introduction {
    .mobile__menu {
        display: none;
    }
    user-select: none;
    //background-color: #14213D;
    color: #fff;
    height: 100%;
    width: 50%;
    top: 0;
    left: 0;
    position: fixed;
    overflow: hidden;
    z-index: 10;
    transition: .85s ease;
    .introduction__menu {
        .introduction__modal {
            transition: .3s;
            z-index: 0;
            background-color: #fff;
            border-radius: 15px;
            padding: 15% 7.5% 7.5% 5%;
            margin: 6%;
            position: absolute;
            width: 25vw;
            height: 52vh;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            align-items: flex-start;
            letter-spacing: 2px;
            white-space: nowrap;
            word-spacing: 15px;
            //NOTE TEST ANIMACJI
            opacity: 0;
            top: -50%;
            left: -50%;
            //transform: translate(-50%, -50%) scale(0.01);
            //transition: opacity 1000ms ease-in-out, top 300ms ease-in-out, left 300ms ease-in-out, transform 300ms ease-in-out;
            //NOTE NOWA WERSJA
            overflow: auto;
            transform-origin: calc(100% - 30px) 30px;
            animation-timing-function: cubic-bezier(0.34, 1.2, 0.64, 1);
            //NOTE WERSJA DESKTOP
            transform-origin: left top;
            &.activeStyle {
                //TEST
                //top: 25%;
                //left: 20%;
                //transform: translate(-50%, -50%) scale(1);
                top: 5.5%;
                left: 0%;
                opacity: 1;
                height: auto;
                width: auto;
                padding-top: 12.5%;
                margin-top: 0%;
                transition: transform 300ms cubic-bezier(0.18, 0.89, 0.43, 1.19);
            }
        }
        li {
            list-style: none;
            font-size: 35px;
            padding: 10% 0 0;
            text-transform: uppercase;
            font-weight: bold;
        }
        li:first-of-type {
            padding-top: 10%;
        }
        a {
            color: #14213D;
            text-decoration: none;
        }
        .introduction__icon {
            color: #fff;
            transition: .05s ease-in-out; //animacje dodać
            cursor: pointer;
            font-size: 60px;
            position: absolute;
            left: 11.5%;
            top: 8%;
            &.activeStyle {
                left: 12.5%;
                color: #14213D;
                font-size: 62.5px;
            }
        }
    }
    .introduction__info {
        transition: .25s;
        text-align: left;
        font-size: 45px;
        position: absolute;
        left: 10%;
        bottom: 40%;
        &.activeStyle {
            bottom: 12.5%;
        }
        a {
            text-decoration: none;
            button {
                color: #fff;
                border: 2px solid #fff;
                text-transform: uppercase;
                background-color: transparent;
                font-size: 20px;
                margin-top: 45px;
                padding: 15px;
                cursor: pointer;
                &:hover {
                    color: #14213D;
                    background-color: #fff;
                }
            }
        }
    }
}

@media screen and (max-width: 1340px) {
    .introduction {
        .introduction__info {
            left: 5%;
            font-size: 43px;
        }
    }
}

@media screen and (max-width: 1024px) {
    .introduction {
        width: 100%;
        z-index: 2;
        height: 100vh;
        position: static;
        .introduction__modal {
            display: none !important;
        }
        .introduction__info {
            left: 0;
            font-size: 40px;
            padding: 0 5%;
            text-align: center;
            position: static;
            margin-top: 12.5%;
        }
        #menu__icon {
            display: none;
        }
        .mobile__menu {
            margin-top: 5vh;
            text-transform: uppercase;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 40px;
            flex-direction: column;
            li {
                padding: 3vh 0;
                display: flex;
                a {
                    color: #fff;
                    text-decoration: none;
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .introduction {
        .introduction__info {
            font-size: 30px;
            padding: 0 5%;
            position: static;
            margin-top: 20%;
        }
        .mobile__menu {
            margin-top: 2.5vh;
            font-size: 35px;
            li {
                padding: 2.5vh 0;
            }
        }
    }
}

// (iPad Air) poziomo
@media (orientation: landscape) and (min-width: 768px) and (max-width: 1180px) {
    .introduction {
        .introduction__menu {
            .introduction__modal.activeStyle {
                //top: 30%;
                //left: 30%;
                width: 30vw;
                height: 55vh;
                letter-spacing: 0.5px;
            }
        }
        .introduction__info {
            bottom: 15%;
            z-index: -1;
            &.activeStyle {
                display: none;
            }
        }
    }
}

// (iPad Air) pionowo
@media (orientation: portrait) and (min-width: 768px) {
    .introduction {
        .mobile__menu {
            font-size: 50px;
            li {
                padding: 3vh 0;
            }
        }
        .introduction__info {
            position: static;
            margin-top: 15%;
            font-size: 45px;
            a {
                button {
                    margin-top: 60px;
                    padding: 25px;
                    font-size: 26px;
                }
            }
        }
    }
}