.skills {
    overflow: hidden;
    position: sticky;
    top: 0;
    padding: 35px 50px;
    text-align: left;
    color: #000;
    font-size: 23px;
    background-color: #E5E5E5;
    z-index: 3;
    min-height: 200vh;
    min-height: 100vh;
    height: auto;
    h1 {
        color: #14213D;
        font-size: 45px;
        margin: 0 0 7% 5%; //7% --> 10% na mobilce
        text-transform: uppercase;
    }
    .row__technologies {
        user-select: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-content: space-around;
        height: 75vh;
        .skill__example {
            position: relative;
            &:hover p {
                opacity: 0;
                transition: .1s;
            }
            &:hover .skill {
                transition: .1s;
                opacity: 1 !important;
            }
            .skill {
                transition: 3s ease;
                width: 12.5vh;
                height: 12.5vh;
                margin: 15px 45px;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                opacity: 0.375;
            }
            p {
                opacity: 1;
                transition-delay: 2.25s;
                text-align: center;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 2.4rem;
            }
        }
    }
}

@media screen and (max-width: 1470px) {
    .skills .row__technologies .skill__example .skill {
        height: 11vh;
        width: 11vh;
    }
    .skills .row__technologies .skill__example p {
        font-size: 2.1rem;
    }
}

@media screen and (max-width: 1440px) {
    .skills {
        padding: 30px 50px 50px;
        h1 {
            font-size: 50px;
            color: #000;
            margin: 0 0 7.5vh;
            text-align: center;
        }
    }
}

@media (orientation: portrait) and (min-width: 768px) {
    .skills {
        padding: 5vh 10vw 7.5vh !important;
        h1 {
            margin: 0 0 8vh;
        }
        .row__technologies {
            .skill__example {
                margin: 2.5vh 0 !important;
                .skill {
                    width: 12.5vh !important;
                    height: 12.5vh !important;
                }
            }
        }
    }
}

@media screen and (max-width: 1024px) {
    .skills {
        position: static;
        padding: 40px 50px 50px;
        .row__technologies {
            .skill__example {
                margin: 3vh 0;
                .skill {
                    width: 15vh;
                    height: 15vh;
                    margin: 0 45px;
                }
                p {
                    font-size: 2.5rem;
                    transition-delay: 0 !important;
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .skills {
        padding: 40px 50px 50px;
        .row__technologies {
            height: auto;
        }
    }
}