.content {
    position: absolute;
    width: 50%;
    right: 0;
    top: 0;
    transition: .75s ease-in;
    overflow: unset;
    &.fixNav {
        overflow: hidden;
    }
}

.profile__picture__test {
    //background-image: url(/img/Hubert.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    user-select: none;
    z-index: -1;
    position: sticky;
    height: 100vh;
    width: calc(50vw - (50vw - 100%) / 2);
    object-fit: cover;
    transition: filter 750ms ease, opacity 1500ms ease;
    opacity: 1;
}

.profile__picture {
    user-select: none;
    z-index: -1;
    position: sticky;
    height: 100vh;
    width: calc(50vw - (50vw - 100%) / 2);
    object-fit: cover;
    top: 0;
    right: 0;
    transition: filter 750ms ease, opacity 1500ms ease;
    opacity: 1;
    //filter: saturate(0) brightness(1) blur(0px);
}

.about__me,
.projects,
.contact,
.section__map {
    overflow: hidden;
    position: sticky;
    top: 0;
    height: 100vh;
    padding: 4vh 50px;
    text-align: left;
    color: #14213D;
    font-size: 23px;
    h1 {
        font-size: 45px;
        margin: 0 0 5% 5%;
        text-transform: uppercase;
    }
}

.about__me {
    background-color: #fff;
    z-index: 2;
}

.projects {
    z-index: 4;
}

.contact {
    background-color: #fff;
    z-index: 5;
}

@media screen and (max-width: 1024px) {
    .about__me,
    .projects,
    .contact,
    .section__map {
        position: static;
        //height: 125vh;
        padding: 5vh 40px;
        color: #000;
        h1 {
            margin: 0;
            text-align: center;
        }
    }
    .content {
        position: static;
        width: 100%;
        .profile__picture,
        .profile__picture__test {
            display: none;
        }
    }
}