.contact {
    background-color: #343a40;
    color: #fff;
    padding: 4vh 40px;
    user-select: none;
    text-transform: uppercase;
    .contact__links {
        width: 45%;
        text-transform: none;
    }
    a {
        text-decoration: none;
        color: #fff;
    }
    .contact__item {
        user-select: text;
        padding: 7.5% 5%;
        .contact__heading {
            text-transform: uppercase;
            font-weight: bold;
            letter-spacing: 2px;
            font-size: 28px;
            padding-bottom: 5px;
        }
        &:nth-of-type(3) svg {
            border-radius: 5px;
        }
    }
    .contact__section {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
    .download__cv {
        display: block;
        width: 245px;
        margin: 0 auto;
    }
    .cv__btn {
        cursor: pointer;
        color: #fff;
        border: 2px solid #fff;
        text-transform: uppercase;
        background-color: transparent;
        font-size: 20px;
        padding: 15px 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 9vh auto 0;
        width: 245px;
        letter-spacing: 0.5px;
        svg {
            font-size: 20px;
            margin-left: 10px;
        }
        &:hover {
            color: #000;
            background-color: #FCA311;
            border: 2px solid #FCA311;
        }
    }
    .contact__icons {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 7.2vh;
        a {
            margin: 0 2vw;
            &:first-of-type:hover {
                color: #9acd32;
            }
            &:nth-of-type(2):hover {
                color: #FCA311;
            }
            &:nth-of-type(3):hover {
                svg {
                    border-radius: 5px;
                    outline: none;
                    stroke: none;
                    background: #fff;
                    fill: #4169e1;
                }
            }
            &:nth-of-type(4):hover {
                color: #000;
            }
        }
        .icon {
            font-size: 35px;
            transition: .2s;
        }
    }
}

@media screen and (max-width: 1440px) {
    .contact {
        h1 {
            margin: 0 0 5%;
            text-align: center;
        }
    }
}

@media screen and (max-width: 1275px) {
    .contact {
        font-size: 22px;
        .contact__item {
            padding: 7.5% 0%;
        }
    }
}

@media (orientation: landscape) and (min-width: 768px) and (max-width: 1180px) {
    .contact {
        position: relative;
        height: auto;
        padding: 5vh 20px;
        .contact__item {
            padding: 6% 5%;
            .contact__heading {
                font-size: 32px;
            }
        }
        h1 {
            font-size: 50px;
            text-align: center;
            margin: 0;
        }
        .contact__section {
            flex-direction: column;
        }
        .form {
            font-size: 18px;
            width: 85%;
            margin: 2.5% auto;
            label {
                margin-top: 7.5%;
            }
            button {
                margin: 6vh auto 2vh;
                width: 55%;
            }
        }
        .contact__links {
            width: 100%;
            text-align: center;
        }
        .cv__btn {
            margin-top: 5vh;
        }
        .contact__icons a {
            margin: 0 2.5vw;
        }
    }
}

@media (orientation: portrait) and (min-width: 768px) {
    .contact {
        position: relative;
        height: auto;
        padding: 5vh 25px;
        .contact__item {
            padding: 5%;
            .contact__heading {
                font-size: 34px;
            }
        }
        h1 {
            font-size: 50px;
        }
        .contact__section {
            flex-direction: column;
        }
        .form {
            font-size: 20px;
            width: 75%;
            margin: 2.5% auto;
            label {
                margin-top: 5%;
            }
            button {
                margin: 5vh auto 0;
                width: 50%;
            }
        }
        .contact__links {
            width: 100%;
            text-align: center;
        }
        .contact__icons {
            margin-top: 5vh;
        }
        .cv__btn {
            margin-top: 2.5vh !important;
        }
    }
}

@media screen and (max-width: 1024px) {
    .contact {
        .cv__btn {
            margin-top: 7.5vh;
        }
        .contact__icons a {
            margin: 0 5vw;
        }
        .contact__links {
            text-align: center;
        }
    }
}

@media screen and (max-width: 768px) {
    .contact {
        height: auto;
        padding: 5vh 20px;
        h1 {
            font-size: 50px;
        }
        .contact__section {
            flex-direction: column;
        }
        .form {
            width: 90%;
            margin: 3vh auto 0;
            button {
                margin: 5vh auto;
            }
        }
        .contact__links {
            width: 100%;
            text-align: center;
        }
        .cv__btn {
            margin-top: 5vh;
        }
        .contact__icons a {
            margin: 0 7vw;
        }
    }
}